.inputFieldContainer {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #31343e;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  margin-top: 16px;
  position: relative;
  width: 100%;
}

.inputField {
  background-color: transparent;
  border: 2px solid #31343e;
  color: #ffffff;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  height: 100%;
  position: absolute;
  width: 100%;
}

.inputField:focus {
  border-color: #6d6f76;
}

.inputField::-webkit-input-placeholder {
  color: #8e8e8e;
  line-height: 61px;
}

.inputField:-ms-input-placeholder {
  color: #8e8e8e;
  line-height: 61px;
}

.inputField::-ms-input-placeholder {
  color: #8e8e8e;
  line-height: 61px;
}

.inputField::placeholder {
  color: #8e8e8e;
  line-height: 61px;
}

.passwordVisibilityToggleIcon {
  fill: #fff8 !important;
}

.passwordVisibilityToggle {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  cursor: pointer;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
}

@media all and (hover: hover) {
  .passwordVisibilityToggle:hover .passwordVisibilityToggleIcon {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
  }
}

@media all and (pointer: coarse) {
  .passwordVisibilityToggle:active .passwordVisibilityToggleIcon, .passwordVisibilityToggle:target .passwordVisibilityToggleIcon {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
  }
}

.inputMessage {
  color: #ffffff;
  display: block;
}

.spinnerContainer {
  overflow: hidden;
  position: absolute !important;
  right: 0;
  -webkit-transition-duration: 200ms;
          transition-duration: 200ms;
}

.spinnerContainer.loading {
  opacity: 1;
}

.spinnerContainer:not(.loading) {
  height: 0 !important;
  opacity: 0;
  width: 0 !important;
}

.spinner {
  border-radius: 50%;
  border-style: solid;
  border-width: 3px;
  height: calc(100% - 1px) !important;
  position: absolute !important;
  width: calc(100% - 1px) !important;
}

.spinner:not(.animatedSpinner) {
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.animatedSpinner {
  -webkit-animation-duration: 550ms;
          animation-duration: 550ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-name: animated-spinner;
          animation-name: animated-spinner;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  border-left-color: transparent !important;
}

@-webkit-keyframes animated-spinner {
  from {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
  to {
    -webkit-transform: rotateZ(359deg);
            transform: rotateZ(359deg);
  }
}

@keyframes animated-spinner {
  from {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
  to {
    -webkit-transform: rotateZ(359deg);
            transform: rotateZ(359deg);
  }
}
