.page {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  text-shadow: rgba(0, 0, 0, 0.4) 0 0 16px;
}

.page,
.page * {
  font-family: Montserrat, sans-serif !important;
}

.pageContent {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 0 !important;
  width: 100%;
}

.main {
  background-color: transparent;
  margin: 0 auto;
  min-height: 100% !important;
  max-width: 1920px;
}

@media only screen and (min-width: 1200px) and (max-width: 1440px) {
  .main {
    --site-padding: 82.5px !important;
  }
}

@media only screen and (min-width: 1441px) and (max-width: 1709px) {
  .main {
    --site-padding: 97.96875px !important;
  }
}

@media only screen and (min-width: 1710px) {
  .main {
    --site-padding: 110px !important;
  }
}

@supports (padding: max(0px)) {
  @media only screen and (max-width: 319px) {
    .main {
      padding-bottom: max(12px, env(safe-area-inset-bottom));
    }
  }
  @media only screen and (min-width: 320px) and (max-width: 419px) {
    .main {
      padding-bottom: max(16px, env(safe-area-inset-bottom));
    }
  }
  @media only screen and (min-width: 420px) and (max-width: 767px) {
    .main {
      padding-bottom: max(20px, env(safe-area-inset-bottom));
    }
  }
  @media only screen and (min-width: 768px) and (max-width: 899px) {
    .main {
      padding-bottom: max(24px, env(safe-area-inset-bottom));
    }
  }
  @media only screen and (min-width: 900px) and (max-width: 1199px) {
    .main {
      padding-bottom: max(28px, env(safe-area-inset-bottom));
    }
  }
  @media only screen and (min-width: 1200px) and (max-width: 1440px) {
    .main {
      padding-bottom: max(var(--site-padding), env(safe-area-inset-bottom));
    }
  }
  @media only screen and (min-width: 1441px) and (max-width: 1709px) {
    .main {
      padding-bottom: max(var(--site-padding), env(safe-area-inset-bottom));
    }
  }
  @media only screen and (min-width: 1710px) {
    .main {
      padding-bottom: max(var(--site-padding), env(safe-area-inset-bottom));
    }
  }
}

