@media only screen and (max-width: 899px) {
  .dataView,
  .dataRow,
  .dataCell {
    display: block;
  }
  .dataRow {
    margin-bottom: 24px;
  }
  .dataRow.isFlex {
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    display: -webkit-box !important;
    display: -ms-flexbox !important;
    display: flex !important;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
  }
  .labelCell {
    margin-bottom: 6px;
  }
}

@media only screen and (min-width: 900px) {
  .dataView {
    border-collapse: collapse;
    display: table;
    width: 100%;
  }
  .dataRow {
    display: table-row;
  }
  .dataCell {
    display: table-cell;
    vertical-align: top;
  }
  .labelCell {
    border-right: 1px solid rgba(112, 112, 112, 0.12);
    width: 40%;
  }
}

.labelTitle {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.labelTitleText {
  font-weight: 600;
  line-height: 1.25;
}

.labelDescription {
  font-family: Montserrat, sans-serif;
  color: #02071a;
  line-height: 1.75;
}

.labelTitleIcon {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

@media only screen and (max-width: 899px) {
  .dataCell.isToggle {
    --toggle-padding-top: (30px / 2);
    --toggle-height: 15px;
  }
  .labelTitleText {
    font-size: 16px;
  }
  .labelDescription {
    font-size: 13px;
  }
  .labelTitleIcon {
