.plan {
  background-color: #ffffff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  position: relative;
}

.plan > * {
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.plan,
.plan * {
  font-family: 'Poppins';
}

.plan:not(.suggested) .name {
  background-color: #5AA5EF48;
  color: #585858;
}

.plan:not(.suggested) .tryPlanButton {
  border-color: #5aa5ef;
  border-width: 1px;
  color: #5aa5ef;
}

@media all and (hover: hover) {
  .plan:not(.suggested) .tryPlanButton:hover {
    background-color: #5aa5ef;
  }
}

@media all and (pointer: coarse) {
  .plan:not(.suggested) .tryPlanButton:active, .plan:not(.suggested) .tryPlanButton:target {
    background-color: #5aa5ef;
  }
}

.plan.suggested::before {
  background-color: #5aa5ef;
  bottom: 100%;
  content: ' ';
  display: block;
  height: 29px;
  left: 0;
  position: absolute;
  width: 100%;
}

.plan.suggested .name {
  background-color: #1172d2;
  color: #fff;
}

.plan.suggested .tryPlanButton {
  background-color: #1172d2;
  border-color: #1172d2;
  color: #fff;
}

@media all and (hover: hover) {
  .plan.suggested .tryPlanButton:hover {
    background-color: #1172d2 !important;
  }
}

