.section {
  background-color: #fff;
  overflow: hidden;
  width: 100%;
}

.sectionTitle {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.sectionTitle > *:first-child {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.videoUploadSection {
  -ms-grid-row: 1;
  -ms-grid-row-span: 2;
  grid-row: 1 / 3;
}

.videoUploadSectionContent {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border: 1px dashed #707070;
  display: -webkit-box;
