  background-color: #c6c6c6;
  color: #000 !important;
}

.button:not(.outlined).whiteColor {
  background-color: #fff;
}

.button.outlined {
  background-color: transparent;
}

.button.outlined.blackColor {
  border-color: #000;
  color: #000;
}

