.notes {
  color: #ddd;
  font-weight: 300;
  line-height: 1.7;
  position: relative;
}

.notes::before {
  background-color: #5aa5ef;
  border-radius: 4px;
  content: ' ';
  display: block;
  height: 100%;
  left: 0;
  position: absolute;
}

@media only screen and (max-width: 767px) {
  .notes::before {
    width: 2px;
  }
}

@media only screen and (min-width: 768px) {
  .notes::before {
    width: 4px;
  }
}

@media only screen and (max-width: 899px) {
